import * as React from "react";
import dayjs from "dayjs";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { PickersDay } from "@mui/x-date-pickers/PickersDay";
import { DateCalendar } from "@mui/x-date-pickers/DateCalendar";
import { DayCalendarSkeleton } from "@mui/x-date-pickers/DayCalendarSkeleton";
import SwipeableButton from "../components/SwipeableButton";

function ServerDay(props) {
  const { day, loggedActivities, dailyActivitiesGoal, ...other } = props;
  const dateKey = day.format("YYYY-MM-DD");
  const activitiesCount = loggedActivities[dateKey]?.length || 0;

  if (activitiesCount === 0) {
    return <PickersDay {...other} day={day} />;
  }

  const progressPercentage = Math.min(
    100,
    (activitiesCount / dailyActivitiesGoal) * 100
  );

  return (
    <PickersDay
      {...other}
      day={day}
      sx={{
        position: "relative",
        zIndex: 1,
        "&.MuiPickersDay-root:not(.Mui-selected)": {
          // prevent double border on today
          border: "none",
        },
        "&.MuiPickersDay-root.Mui-selected, &.Mui-selected:focus, &.Mui-selected:hover":
          {
            backgroundColor: "pink !important",
          },
        "&.Mui-selected::after, &.Mui-selected:hover::after": {
          // days with data
          backgroundColor: "#3377bb",
        },
        "&::before": {
          content: '""',
          position: "absolute",
          top: "0px",
          left: "0px",
          right: "0px",
          bottom: "0px",
          borderRadius: "50%",
          background: `conic-gradient(#5dad51 ${progressPercentage}%, #fdf6e3 ${progressPercentage}%)`,
          zIndex: -2,
          border: "1px solid #555",
        },
        "&::after": {
          content: '""',
          position: "absolute",
          width: "70%",
          height: "70%",
          background: "#fdf6e3", // sand
          borderRadius: "50%",
          zIndex: -1,
          border: "1px solid #555",
        },
      }}
    />
  );
}

export default function DateCalendarServerRequest() {
  const [isLoading, setIsLoading] = React.useState(false);
  const [selectedDate, setSelectedDate] = React.useState(dayjs());
  const [loggedActivities, setLoggedActivities] = React.useState(() => {
    try {
      return JSON.parse(localStorage.getItem("loggedActivities")) || {};
    } catch {
      return {};
    }
  });
  const [dailyActivitiesGoal] = React.useState(() => {
    try {
      const goal = localStorage.getItem("dailyActivitiesGoal");
      return goal ? parseInt(goal, 10) : 3;
    } catch {
      return 3;
    }
  });
  const [activeIndex, setActiveIndex] = React.useState(null);
  const [offset, setOffset] = React.useState(0);
  const [confirming, setConfirming] = React.useState(false);
  const [deletingIndex] = React.useState(null);

  const activities = React.useMemo(() => {
    const dateKey = selectedDate.format("YYYY-MM-DD");
    return loggedActivities[dateKey] || [];
  }, [selectedDate, loggedActivities]);

  const displayState = React.useMemo(
    () => ({
      date: selectedDate,
      isToday: selectedDate.format("YYYYMMDD") === dayjs().format("YYYYMMDD"),
      activitiesCount: activities.length,
      goalMet: activities.length >= dailyActivitiesGoal,
      progressPercentage: Math.min(
        100,
        (activities.length / dailyActivitiesGoal) * 100
      ),
    }),
    [selectedDate, activities, dailyActivitiesGoal]
  );

  const handleMonthChange = (date) => {
    setIsLoading(true);
    setTimeout(() => setIsLoading(false), 500);
  };

  const handleDayChange = (date) => {
    setSelectedDate(date);
    // Reset deletion progress for SwipeableButtons
    setActiveIndex(null);
    setOffset(0);
    setConfirming(false);
  };

  const handleDelete = (index) => {
    const dateKey = selectedDate.format("YYYY-MM-DD");
    const updatedActivities = activities.filter((_, i) => i !== index);

    setLoggedActivities((prev) => {
      const newLoggedActivities = { ...prev };
      newLoggedActivities[dateKey] = updatedActivities;
      localStorage.setItem(
        "loggedActivities",
        JSON.stringify(newLoggedActivities)
      );
      return newLoggedActivities;
    });
  };

  return (
    <div>
      <LocalizationProvider dateAdapter={AdapterDayjs}>
        <DateCalendar
          value={selectedDate}
          onChange={handleDayChange}
          loading={isLoading}
          onMonthChange={handleMonthChange}
          renderLoading={() => <DayCalendarSkeleton />}
          slots={{
            day: ServerDay,
          }}
          slotProps={{
            day: {
              sx: {
                "&.Mui-selected, &.Mui-selected:focus, &.Mui-selected:hover": {
                  backgroundColor: "#3377bb",
                },
              },
              loggedActivities,
              dailyActivitiesGoal,
            },
          }}
          shouldDisableDate={(date) => date.isAfter(dayjs())}
        />
      </LocalizationProvider>
      <div className="px-4 max-w-96 mx-auto">
        <h2 className="text-lg m-1 font-semibold">
          {displayState.date.format("MMMM D, YYYY")}
        </h2>
        {displayState.isToday ? (
          displayState.activitiesCount === 0 ? (
            <div className="m-1 mb-3">
              <p>You haven't logged any activities yet.</p>
              <p className="text-sm text-gray-700">There's still time! </p>
            </div>
          ) : (
            <div className="m-1 mb-3">
              <p>
                You've logged {displayState.activitiesCount} activit
                {displayState.activitiesCount === 1 ? "y" : "ies"} so far.
              </p>
              {displayState.goalMet ? (
                <p className="text-sm text-gray-700">Great work! </p>
              ) : (
                <p className="text-sm text-gray-700">Keep it up! </p>
              )}
            </div>
          )
        ) : displayState.activitiesCount === 0 ? (
          <div className="m-1 mb-3">
            <p>No activities logged for this day.</p>
            <p className="text-sm text-gray-700">Next time! </p>
          </div>
        ) : (
          <div className="m-1 mb-3">
            <p>
              You logged {displayState.activitiesCount} activit
              {displayState.activitiesCount === 1 ? "y" : "ies"}.
            </p>
            {displayState.goalMet ? (
              <p className="text-sm text-gray-700">Great work! </p>
            ) : (
              <p className="text-sm text-gray-700">On the right track! </p>
            )}
          </div>
        )}
        <ul>
          {activities.map(([activity, time], idx) => (
            <SwipeableButton
              key={time}
              activity={activity}
              time={dayjs(time).format("HH:mm")}
              index={idx}
              onLog={() => console.log(`Logged: ${activity}`)}
              onDelete={handleDelete}
              activeIndex={activeIndex}
              setActiveIndex={setActiveIndex}
              confirming={activeIndex === idx ? confirming : false}
              setConfirming={setConfirming}
              offset={activeIndex === idx ? offset : 0}
              setOffset={setOffset}
              deletingIndex={deletingIndex}
              forceOffset={undefined}
            />
          ))}
        </ul>
      </div>
    </div>
  );
}
