import React, { useState } from "react";
import { Link } from "react-router-dom";

// resources page with same header style/background as Home
const Resources = () => {
  const [isArticlesOpen, setIsArticlesOpen] = useState(false);

  // curated articles: Title  Source with links
  const articles = [
    {
      title: "A Comprehensive Review on Postpartum Depression",
      source: "Cureus (PMC)",
      url: "https://pmc.ncbi.nlm.nih.gov/articles/PMC9851410/",
    },
    {
      title: "Postpartum depression: The worst kept secret",
      source: "Harvard Health Publishing",
      url: "https://www.health.harvard.edu/blog/postpartum-depression-worst-kept-secret-2017020811008",
    },
    {
      title: "Postpartum depression",
      source: "Mayo Clinic",
      url: "https://www.mayoclinic.org/diseases-conditions/postpartum-depression/symptoms-causes/syc-20376617",
    },
    {
      title: "Perinatal Depression",
      source: "StatPearls (NCBI Bookshelf)",
      url: "https://www.ncbi.nlm.nih.gov/books/NBK519070/",
    },
    {
      title: "Timing of Postpartum Depressive Symptoms",
      source: "CDC  Preventing Chronic Disease",
      url: "https://www.cdc.gov/pcd/issues/2023/23_0107.htm",
    },
    {
      title: "Life in the Throes of Postpartum Depression",
      source: "The New York Times",
      url: "https://www.nytimes.com/2023/06/27/well/mind/postpartum-depression-mental-health.html",
    },
  ];

  return (
    <div className="bg-sand text-paynesGray min-h-screen">
      <header className="bg-powderBlue-300 w-full rounded-b-xl shadow-lg text-center p-4 sm:py-5 fixed top-0 left-0 z-10">
        <h1 className="w-full text-2xl sm:text-4xl text-paynesGray font-bold">
          Resources
        </h1>
      </header>

      <main className="pt-24 sm:pt-28 pb-20 px-4 flex flex-col items-center max-sm:text-sm text-center">
        <div className="w-full max-w-lg grid grid-cols-2 gap-4 mb-10">
          {/* EPDS Form */}
          <Link
            to="/epds"
            className="bg-white p-4 rounded-xl shadow-lg flex flex-col items-center justify-center active:bg-powderBlue-100 transition-colors"
          >
            <i className="fa-solid fa-comment-medical text-3xl mb-2"></i>
            <span>Edinburgh Postnatal Depression Scale</span>
          </Link>

          {/* Articles (opens modal) */}
          <button
            type="button"
            onClick={() => setIsArticlesOpen(true)}
            className="bg-white p-4 rounded-xl shadow-lg flex flex-col items-center justify-center active:bg-powderBlue-100 transition-colors focus:outline-none focus:ring-2 focus:ring-powderBlue-300"
          >
            <i className="fa-solid fa-book text-3xl mb-2"></i>
            <span>Articles</span>
          </button>
        </div>

        <h2 className="font-semibold text-lg mb-3">External Links</h2>
        <div className="w-full max-w-lg grid grid-cols-2 gap-4 mb-6">
          {/* POEM - Mental Health Association of Ohio */}
          <a
            href="https://mhaohio.org/poem/"
            target="_blank"
            rel="noopener noreferrer"
            className="bg-white p-4 rounded-xl shadow-lg flex flex-col items-center justify-center active:bg-powderBlue-100 transition-colors"
          >
            <i className="fa-solid fa-heart text-3xl mb-2"></i>
            <span>POEM Program</span>
          </a>

          {/* Postpartum Support International */}
          <a
            href="https://postpartum.net/"
            target="_blank"
            rel="noopener noreferrer"
            className="bg-white p-4 rounded-xl shadow-lg flex flex-col items-center justify-center active:bg-powderBlue-100 transition-colors"
          >
            <i className="fa-solid fa-hands-helping text-3xl mb-2"></i>
            <span>Postpartum Support</span>
          </a>

          {/* NAMI Maternal Mental Health */}
          <a
            href="https://www.nami.org/your-journey/maternal-new-parent-mental-health/"
            target="_blank"
            rel="noopener noreferrer"
            className="bg-white p-4 rounded-xl shadow-lg flex flex-col items-center justify-center active:bg-powderBlue-100 transition-colors"
          >
            <i className="fa-solid fa-baby text-3xl mb-2"></i>
            <span>NAMI Maternal Health</span>
          </a>

          {/* Holistic Consultation LLC */}
          <a
            href="https://holisticconsultationllc.com/perinatal-mental-health-columbus-oh/"
            target="_blank"
            rel="noopener noreferrer"
            className="bg-white p-4 rounded-xl shadow-lg flex flex-col items-center justify-center active:bg-powderBlue-100 transition-colors"
          >
            <i className="fa-solid fa-leaf text-3xl mb-2"></i>
            <span>Holistic Perinatal Care</span>
          </a>
        </div>

        <div className="w-full max-w-lg grid grid-cols-2 gap-4">
          {/* Meditation Guide (external link) */}
          <a
            href="https://www.calm.com/blog/postpartum-meditation"
            target="_blank"
            rel="noopener noreferrer"
            className="bg-white p-4 rounded-xl shadow-lg flex flex-col items-center justify-center active:bg-powderBlue-100 transition-colors"
          >
            <img
              src="https://img.icons8.com/4a6572/ios-filled/512/female-meditation"
              alt="Meditation"
              className="w-9 h-9 mb-2"
            />
            {/* <i className="fa-solid fa-person-praying text-3xl mb-2"></i> */}
            <span>Meditation Guide</span>
          </a>

          {/* Gentle Exercise (external link) */}
          <a
            href="https://www.mayoclinic.org/healthy-lifestyle/labor-and-delivery/in-depth/exercise-after-pregnancy/art-20044596"
            target="_blank"
            rel="noopener noreferrer"
            className="bg-white p-4 rounded-xl shadow-lg flex flex-col items-center justify-center active:bg-powderBlue-100 transition-colors"
          >
            <i className="fa-solid fa-dumbbell text-3xl mb-2"></i>
            <span>Gentle Exercise</span>
          </a>

          {/* Support Groups (external link) */}
          <a
            href="https://www.postpartumdepression.org/support/groups/"
            target="_blank"
            rel="noopener noreferrer"
            className="bg-white p-4 rounded-xl shadow-lg flex flex-col items-center justify-center active:bg-powderBlue-100 transition-colors"
          >
            <i className="fa-solid fa-people-group text-3xl mb-2"></i>
            <span>Support Groups</span>
          </a>

          {/* Hotline (external link) */}
          <a
            href="https://mchb.hrsa.gov/programs-impact/national-maternal-mental-health-hotline"
            target="_blank"
            rel="noopener noreferrer"
            className="bg-white p-4 rounded-xl shadow-lg flex flex-col items-center justify-center active:bg-powderBlue-100 transition-colors"
          >
            <i className="fa-solid fa-headset text-3xl mb-2"></i>
            <span>Hotline</span>
          </a>
        </div>
      </main>

      {/* Articles Popup */}
      {isArticlesOpen && (
        <div
          className="fixed inset-0 z-20 flex items-center justify-center bg-black bg-opacity-50 p-4"
          role="dialog"
          aria-modal="true"
          aria-labelledby="articles-modal-title"
          onClick={() => setIsArticlesOpen(false)}
        >
          <div
            className="bg-white rounded-2xl shadow-xl max-w-2xl w-full p-6 relative"
            onClick={(e) => e.stopPropagation()}
          >
            <button
              type="button"
              className="absolute top-3 right-3 text-paynesGray hover:text-black"
              aria-label="Close"
              onClick={() => setIsArticlesOpen(false)}
            >
              <i className="fa-solid fa-xmark text-xl"></i>
            </button>
            <h2
              id="articles-modal-title"
              className="text-xl sm:text-2xl font-bold text-paynesGray mb-4"
            >
              Articles
            </h2>
            <p className="text-sm text-paynesGray/80 mb-4">
              Curated reading on postpartum mental health. Each link opens in a
              new tab.
            </p>
            <ul className="list-disc pl-5 space-y-2">
              {articles.map((a, idx) => (
                <li key={idx} className="text-paynesGray">
                  <a
                    href={a.url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="underline underline-offset-2 hover:text-powderBlue-700"
                  >
                    {a.title}  {a.source}
                  </a>
                </li>
              ))}
            </ul>
            <div className="mt-6 flex justify-end">
              <button
                type="button"
                className="px-4 py-2 rounded-lg bg-powderBlue-300 text-paynesGray font-medium shadow hover:bg-powderBlue-200"
                onClick={() => setIsArticlesOpen(false)}
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Resources;
