import React, { useState } from "react";
import dayjs from "dayjs";
import { useNavigate } from "react-router-dom";

const JoySetupActivities = (props) => {
  const navigate = useNavigate();

  // State for default activities with their "liked" status
  const [defaultActivities, setDefaultActivities] = useState([
    { name: "Reading ", liked: false },
    { name: "Taking a walk ", liked: false },
    { name: "Drinking coffee ", liked: false },
    { name: "Drinking tea ", liked: false },
    { name: "Watching a TV show ", liked: false },
  ]);

  // State for custom activities
  const [customActivities, setCustomActivities] = useState([""]);

  // Handle checkbox toggle for default activities
  const handleCheckboxChange = (index) => {
    const updatedActivities = [...defaultActivities];
    updatedActivities[index].liked = !updatedActivities[index].liked;
    setDefaultActivities(updatedActivities);
  };

  // Handle input change for custom activities
  const handleCustomInputChange = (value, index) => {
    const updatedCustomActivities = [...customActivities];
    updatedCustomActivities[index] = value;

    // Add a new input if the current one is not empty and is the last input
    if (value !== "" && index === customActivities.length - 1) {
      updatedCustomActivities.push("");
    }

    // Remove the input if it's empty and not the last one
    if (value === "" && index !== customActivities.length - 1) {
      updatedCustomActivities.splice(index, 1);

      // Focus the last input
      setTimeout(() => {
        const lastInput = document.querySelector(`input[value='']`);
        if (lastInput) {
          lastInput.focus();
        }
      }, 0);
    }

    setCustomActivities(updatedCustomActivities);
  };

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();

    // Combine liked default activities and non-empty custom activities
    const likedActivities = [
      ...defaultActivities
        .filter((activity) => activity.liked)
        .map((activity) => activity.name),
      ...customActivities.filter((activity) => activity.trim() !== ""),
    ];

    console.log("Liked activities:", likedActivities);
    localStorage.setItem("likedActivities", JSON.stringify(likedActivities));
    localStorage.setItem(
      "loggedActivities",
      JSON.stringify({ [dayjs().format("YYYY-MM-DD")]: [] })
    );

    navigate("/joy/setup/goal");
  };

  return (
    <form onSubmit={handleSubmit}>
      <fieldset className="p-4">
        <legend className="text-xl font-semibold pt-4">
          How do you take care of yourself?
        </legend>
        <p>You can add or remove items later.</p>
        <ul className="space-y-2">
          {/* Render default activities with checkboxes */}
          {defaultActivities.map((activity, index) => (
            <li key={index}>
              <label
                className={`block p-3 shadow-md rounded-lg cursor-pointer ${
                  activity.liked ? "bg-powderBlue-400" : "bg-gray-100"
                } active:bg-powderBlue-200`}
                onClick={() => handleCheckboxChange(index)}
              >
                <input
                  type="checkbox"
                  checked={activity.liked}
                  onChange={() => handleCheckboxChange(index)}
                  className="hidden"
                />
                <span className="ml-2">{activity.name}</span>
              </label>
            </li>
          ))}

          {/* Render custom activity input fields */}
          {customActivities.map((activity, index) => (
            <li key={`custom-${index}`}>
              <input
                type="text"
                className={`manual p-3 shadow-md rounded-lg w-full ${
                  activity.trim() !== "" ? "bg-powderBlue-300" : "bg-gray-100"
                }`}
                placeholder="Add a joyful activity..."
                value={activity}
                onChange={(e) => handleCustomInputChange(e.target.value, index)}
              />
            </li>
          ))}
        </ul>
        <button
          type="submit"
          className="block font-semibold rounded-3xl mt-4 bg-powderBlue-700 active:bg-powderBlue-800 text-white px-4 py-2 mx-auto"
        >
          Next
        </button>
      </fieldset>
    </form>
  );
};

export default JoySetupActivities;
