import React, { useState } from "react";
import { useSwipeable } from "react-swipeable";

const REVEAL_OFFSET = -60; // px for partial reveal
const DELETE_THRESHOLD = -100; // px for full delete
const DELETE_ANIMATION_OFFSET = -500; // px to slide off screen
const DELETE_ANIMATION_DURATION = 300; // ms

const SwipeableButton = ({
  activity,
  time,
  index,
  onLog,
  onDelete,
  activeIndex,
  setActiveIndex,
  confirming,
  setConfirming,
  offset,
  setOffset,
  forceOffset,
  loggable,
}) => {
  const [isDeleting, setIsDeleting] = useState(false); // Local state for delete animation
  const [swiped, setSwiped] = useState(false);

  const buttonStyle = {
    touchAction: "pan-y",
    opacity: isDeleting ? 0 : 1,
    transform: isDeleting
      ? `translateX(${DELETE_ANIMATION_OFFSET}px)`
      : `translateX(${
          typeof forceOffset === "number"
            ? forceOffset
            : activeIndex === index
            ? offset
            : 0
        }px)`,
    transition: isDeleting
      ? `transform ${DELETE_ANIMATION_DURATION}ms cubic-bezier(.4,0,.2,1), opacity ${DELETE_ANIMATION_DURATION}ms`
      : "transform 200ms cubic-bezier(.4,0,.2,1)",
  };

  const swipeHandlers = useSwipeable({
    onSwiping: (eventData) => {
      setSwiped(true);
      if (activeIndex !== index) {
        setActiveIndex(index);
        setConfirming(false);
        setOffset(0);
      }
      if (eventData.dir === "Left") {
        if (!confirming) {
          setOffset(Math.max(eventData.deltaX, REVEAL_OFFSET));
        } else {
          setOffset(eventData.deltaX);
        }
      }
      if (eventData.dir === "Right") {
        setOffset(Math.min(eventData.deltaX, 0));
      }
    },
    onSwipedLeft: (eventData) => {
      if (!confirming && eventData.deltaX <= REVEAL_OFFSET) {
        setOffset(REVEAL_OFFSET);
        setConfirming(true);
        setActiveIndex(index);
      } else if (confirming && eventData.deltaX <= DELETE_THRESHOLD) {
        triggerDelete(); // Trigger delete with animation
      } else {
        setOffset(0);
        setConfirming(false);
        setActiveIndex(null);
      }
      setTimeout(() => setSwiped(false), 0);
    },
    onSwipedRight: () => {
      setOffset(0);
      setConfirming(false);
      setActiveIndex(null);
      setTimeout(() => setSwiped(false), 0);
    },
    preventDefaultTouchmoveEvent: true,
    trackMouse: true,
  });

  const triggerDelete = () => {
    setIsDeleting(true); // Start delete animation
    setTimeout(() => {
      onDelete(index); // Call the delete callback after animation
      setIsDeleting(false); // Reset delete state
      setActiveIndex(null); // Reset activeIndex
      setOffset(0); // Reset offset
    }, DELETE_ANIMATION_DURATION);
  };

  const handleTrashClick = (e) => {
    e.stopPropagation();
    if (confirming || forceOffset === REVEAL_OFFSET) {
      triggerDelete(); // Trigger delete with animation
    }
  };

  const handleItemClick = (e) => {
    if (swiped) {
      return;
    }
    if (confirming) {
      setOffset(0);
      setConfirming(false);
      setActiveIndex(null);
    } else if (loggable) {
      onLog(e);
    }
  };

  return (
    <li
      className="relative flex justify-center items-center mb-3"
      {...swipeHandlers}
    >
      <button
        type="button"
        className={`absolute right-4 top-1/2 -translate-y-1/2 p-2 ${
          confirming || forceOffset === REVEAL_OFFSET
            ? "pointer-events-auto cursor-pointer"
            : "pointer-events-none cursor-default"
        }`}
        onClick={handleTrashClick}
        aria-label="Delete activity"
      >
        <i className="fa-solid fa-trash text-rose-600 text-xl"></i>
      </button>
      <button
        type="button"
        className="block px-5 py-3 shadow-md rounded-lg bg-gray-100 active:bg-powderBlue-200 cursor-pointer w-full text-left"
        name={activity}
        onClick={handleItemClick}
        style={buttonStyle}
      >
        <div className="flex justify-between items-center">
          <span>{activity}</span>
          {time && <span className="text-sm text-gray-500">{time}</span>}
          {/* Display time if provided */}
        </div>
      </button>
    </li>
  );
};

export default SwipeableButton;
