import React, { useState, useEffect, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import "./WaterLog.css";

const weekdays = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"];

const getWeekStart = (dateStr) => {
  const d = new Date(dateStr);
  const day = d.getDay();
  d.setDate(d.getDate() - day);
  d.setHours(0, 0, 0, 0);
  return d.toISOString().split("T")[0];
};

const hourFromTime = (timeStr) => {
  if (!timeStr || typeof timeStr !== "string") return 0;
  const [h] = timeStr.split(":").map((v) => parseInt(v, 10));
  return Number.isFinite(h) ? h : 0;
};

const bucketByPeriod = (hour) => {
  if (hour >= 5 && hour < 12) return "Morning";
  if (hour >= 12 && hour < 17) return "Afternoon";
  if (hour >= 17 && hour < 22) return "Evening";
  return "Night";
};

const toOz = (ml) => ml / 29.5735;

const WaterLog = () => {
  const [logs, setLogs] = useState([]);
  const [unit, setUnit] = useState("oz"); // default unit is ounces
  const navigate = useNavigate();

  useEffect(() => {
    const savedLogs = JSON.parse(localStorage.getItem("waterLogs")) || [];
    setLogs(savedLogs);
  }, []);

  const handleDelete = (id) => {
    const updated = logs.filter((log) => log.id !== id);
    setLogs(updated);
    localStorage.setItem("waterLogs", JSON.stringify(updated));
  };

  // Group logs into weeks
  const weeks = useMemo(() => {
    const grouped = logs.reduce((acc, log) => {
      const weekStart = getWeekStart(log.date);
      if (!acc[weekStart]) acc[weekStart] = [];
      acc[weekStart].push(log);
      return acc;
    }, {});
    Object.keys(grouped).forEach((k) => {
      grouped[k].sort((a, b) => {
        const da = new Date(`${a.date}T${a.time || "00:00"}`);
        const db = new Date(`${b.date}T${b.time || "00:00"}`);
        return db - da;
      });
    });
    return grouped;
  }, [logs]);

  const weekKeys = useMemo(() => Object.keys(weeks).sort(), [weeks]);

  // Weekly averages converted based on selected unit
  const weeklyAverages = useMemo(() => {
    return weekKeys.map((week) => {
      const totalMl = weeks[week].reduce((sum, l) => sum + l.volume, 0); // stored in ml
      const avgMl = totalMl / 7;
      return {
        weekStart: week,
        average: unit === "ml" ? avgMl : toOz(avgMl),
      };
    });
  }, [weekKeys, weeks, unit]);

  // Patterns (always computed in ml, display not affected)
  const weekdayWinner = useMemo(() => {
    const totals = {};
    logs.forEach((log) => {
      const wd = weekdays[new Date(log.date).getDay()];
      totals[wd] = (totals[wd] || 0) + log.volume;
    });
    return Object.entries(totals).sort((a, b) => b[1] - a[1])[0]?.[0] || "N/A";
  }, [logs]);

  const periodWinner = useMemo(() => {
    const totals = { Morning: 0, Afternoon: 0, Evening: 0, Night: 0 };
    logs.forEach((log) => {
      const hr = hourFromTime(log.time);
      const p = bucketByPeriod(hr);
      totals[p] += log.volume;
    });
    return Object.entries(totals).sort((a, b) => b[1] - a[1])[0]?.[0] || "N/A";
  }, [logs]);

  return (
    <div className="waterlog-container">
      <h1 className="waterlog-title">Water Intake History</h1>

      <div className="log-controls">
        <button className="back-button" onClick={() => navigate("/water")}>
           Back to Log Page
        </button>
        <button
          className="unit-toggle-btn"
          onClick={() => setUnit(unit === "ml" ? "oz" : "ml")}
        >
          Switch Unit to {unit === "ml" ? "oz" : "ml"}
        </button>
      </div>

      {logs.length === 0 ? (
        <p>No logs yet.</p>
      ) : (
        <>
          {weekKeys.map((week) => (
            <div key={week} className="weekly-block">
              <h3>Week starting {week}</h3>
              <table className="waterlog-table">
                <thead>
                  <tr>
                    <th>Date</th>
                    <th>Time</th>
                    <th>Weekday</th>
                    <th>Water Intake ({unit})</th>
                    <th>Delete</th>
                  </tr>
                </thead>
                <tbody>
                  {weeks[week].map((log) => (
                    <tr key={log.id}>
                      <td>{log.date}</td>
                      <td>{log.time}</td>
                      <td>{weekdays[new Date(log.date).getDay()]}</td>
                      <td>
                        {unit === "ml"
                          ? log.volume
                          : toOz(log.volume).toFixed(1)}
                      </td>
                      <td>
                        <button
                          className="delete-button"
                          onClick={() => handleDelete(log.id)}
                        >
                          Delete
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ))}
        </>
      )}

      {logs.length > 0 && (
        <div className="analysis-section">
          <h2>Analysis</h2>
          <div className="pattern-flow">
            <div className="pattern-flow__title">Drinking Pattern</div>
            <div className="pattern-flow__content">
              <div className="pattern-chip">
                Most water day: <strong>{weekdayWinner}</strong>
              </div>
              <div className="pattern-chip">
                Peak period: <strong>{periodWinner}</strong>
              </div>
            </div>
          </div>

          {/* Weekly averages reflect selected unit */}
          <h3>Average Water Intake Per Week</h3>
          <table className="analysis-table">
            <thead>
              <tr>
                <th>Week Starting</th>
                <th>Average Intake ({unit}/day)</th>
              </tr>
            </thead>
            <tbody>
              {weeklyAverages.map((w) => (
                <tr key={w.weekStart}>
                  <td>{w.weekStart}</td>
                  <td>
                    {w.average.toFixed(1)} {unit}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default WaterLog;
