import React from "react";
import { Link } from "react-router-dom";

const Home = () => {
  return (
    <div className="bg-sand text-paynesGray">
      <header className="bg-powderBlue-300 w-full rounded-b-xl shadow-lg text-center p-4 sm:py-5 fixed top-0 left-0 z-10">
        <h1 className="w-full text-2xl sm:text-4xl text-paynesGray font-bold">
          Healthy at Home Together
        </h1>
      </header>

      <main className="flex flex-col justify-around items-center h-screen py-8">
        <div className="flex flex-wrap gap-8 mx-3">
          {/** Mom */}
          <div className="w-full bg-teaGreen text-paynesGray p-4 rounded-xl shadow-lg">
            <Link to="/mom">
              <h2 className="py-2 font-bold text-center text-lg sm:text-xl">
                Mom
              </h2>
            </Link>
            <div className="bg-white p-4 rounded-xl flex items-center justify-around sm:justify-between w-full shadow-lg">
              <div className="flex flex-col items-center">
                <Link to="/water" className="flex flex-col items-center">
                  <i className="fa-solid fa-bottle-water text-2xl"></i>
                  <span className="hidden sm:block text-sm mt-1">
                    Hydration
                  </span>
                </Link>
              </div>

              <div className="flex flex-col items-center">
                <i className="fa-solid fa-utensils text-2xl"></i>
                <span className="hidden sm:block text-sm mt-1">Nutrition</span>
              </div>

              <div className="flex flex-col items-center">
                {/* Meditation (no link) */}
                <img
                  src="https://img.icons8.com/4a6572/ios-filled/512/female-meditation"
                  alt="Meditation"
                  className="w-8 h-8"
                />
                {/* <i className="fa-solid fa-person-praying text-2xl"></i> */}
                <span className="hidden sm:block text-sm mt-1">Meditation</span>
              </div>

              <div className="flex flex-col items-center">
                <Link to="/joy">
                  <i className="fa-solid fa-brain text-2xl"></i>
                </Link>
                <span className="hidden sm:block text-sm mt-1">Self Care</span>
              </div>
            </div>
          </div>

          {/** Baby */}
          <div className="w-full bg-rose text-paynesGray p-4 rounded-xl shadow-lg">
            <h2 className="py-2 font-bold text-lg text-center sm:text-xl">
              Baby
            </h2>

            <div className="bg-white p-4 rounded-xl flex items-center justify-around sm:justify-between w-full shadow-lg">
              <div className="flex flex-col items-center">
                <i className="fa-solid fa-bottle-water text-2xl"></i>
                {/** Need to replace with baby bottle */}
                <span className="hidden sm:block text-sm mt-1">Nutrition</span>
              </div>

              <div className="flex flex-col items-center">
                <i className="fa-solid fa-moon text-2xl"></i>
                <span className="hidden sm:block text-sm mt-1">Sleep</span>
              </div>

              <div className="flex flex-col items-center">
                <i className="fa-solid fa-toilet text-2xl"></i>
                <span className="hidden sm:block text-sm mt-1">Diaper Log</span>
              </div>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
};

export default Home;
