import React, { useState, useEffect } from "react";
import { logActivity } from "../utils/joyLogger";
import { useNavigate, Link } from "react-router-dom";
import SwipeableButton from "../components/SwipeableButton";

const REVEAL_OFFSET = -60; // px for partial reveal

const JoyApp = () => {
  const [likedActivities, setLikedActivities] = useState([]);
  const [activeIndex, setActiveIndex] = useState(null);
  const [confirming, setConfirming] = useState(false);
  const [offset, setOffset] = useState(0);
  const [deletingIndex] = useState(null);
  const [editMode, setEditMode] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const stored = localStorage.getItem("likedActivities");
    if (!stored) {
      navigate("/joy/setup/activities", { replace: true });
    } else {
      setLikedActivities(JSON.parse(stored));
    }
  }, [navigate]);

  const [expanding, setExpanding] = useState(false);
  const [expandStyle, setExpandStyle] = useState(null);
  const [expandActivity, setExpandActivity] = useState(null);
  const [originalStyle, setOriginalStyle] = useState(null);
  const [textVisible, setTextVisible] = useState(false);

  const handleLog = (e) => {
    e.preventDefault();
    const btn = e.currentTarget;
    const rect = btn.getBoundingClientRect();
    const style = {
      left: rect.left,
      top: rect.top,
      width: rect.width,
      height: rect.height,
    };
    setOriginalStyle(style); // Save original
    setExpandStyle(style);
    setExpandActivity(btn.name);
    setExpanding(true);
    logActivity(btn.name);
  };

  useEffect(() => {
    if (expanding) {
      document.body.classList.add("pointer-events-none"); // Disable buttons
      document.body.style.overflow = "hidden"; // Prevent scroll

      setTimeout(() => {
        setExpandStyle({
          left: 0,
          top: 0,
          width: window.innerWidth,
          height: window.innerHeight,
          transition: "all 0.7s cubic-bezier(.4,0,.2,1)",
          borderRadius: 0,
          fontSize: "2rem",
          zIndex: 1000,
        });
        setTextVisible(true); // Fade in
      }, 10);

      setTimeout(() => {
        setExpandStyle({
          ...originalStyle,
          transition: "all 0.7s cubic-bezier(.4,0,.2,1)",
          borderRadius: "0.75rem",
          fontSize: "1rem",
          zIndex: 1000,
        });
        setTextVisible(false); // Fade out
      }, 1750);

      setTimeout(() => {
        setExpanding(false);
        setExpandStyle(null);
        setExpandActivity(null);
        setOriginalStyle(null);
        document.body.style.overflow = ""; // Restore scroll
        document.body.classList.remove("pointer-events-none"); // Re-enable buttons
      }, 2750);
    }
  }, [expanding, originalStyle, navigate]);

  const handleNewActivity = (e) => {
    e.preventDefault();
    navigate("/joy/new");
  };

  // Animate then delete
  const handleDelete = (index) => {
    const updated = likedActivities.filter((_, i) => i !== index);
    setLikedActivities(updated);
    localStorage.setItem("likedActivities", JSON.stringify(updated));
  };

  // Edit/Done button handler
  const handleEditToggle = () => {
    setEditMode((prev) => !prev);
    setActiveIndex(null);
    setConfirming(false);
    setOffset(0);
  };

  return (
    <div className="relative">
      {expanding && (
        <button
          className="fixed bg-gray-100 shadow font-bold rounded-xl overflow-hidden"
          style={{
            ...expandStyle,
            transition: expandStyle?.transition || "none",
          }}
        >
          <div
            style={{
              opacity: textVisible ? 1 : 0,
              transition: "opacity 0.5s cubic-bezier(.4,0,.2,1)",
            }}
          >
            <p className="text-2xl text-gray-600 m-3">{expandActivity}</p>
            <p className="text-5xl"> Way to go!</p>
          </div>
        </button>
      )}

      <header className="bg-powderBlue-300 w-full rounded-b-xl shadow-lg text-center p-4 sm:py-5 fixed top-0 left-0 z-10">
        <h1 className="w-full text-2xl sm:text-4xl  text-paynesGray font-bold">
          Self Care
        </h1>
      </header>

      <main className="pt-24 sm:pt-28 pb-8 px-4 max-w-96 mx-auto">
        <form>
          <fieldset>
            <div className="flex justify-between items-center pb-4">
              <legend className="text-xl font-semibold">Log an activity</legend>
              {/* Edit/Cancel button */}
              {likedActivities.length > 0 && (
                <button
                  type="button"
                  className="h-min px-3 py-1 bg-paynesGray-500 active:bg-paynesGray-600 shadow text-md text-white rounded-3xl"
                  onClick={handleEditToggle}
                >
                  {editMode ? "Done" : "Edit"}
                </button>
              )}
            </div>
            <ul>
              {likedActivities.map((activity, index) => (
                <SwipeableButton
                  key={activity}
                  activity={activity}
                  index={index}
                  onLog={handleLog}
                  onDelete={handleDelete}
                  activeIndex={activeIndex}
                  setActiveIndex={setActiveIndex}
                  confirming={activeIndex === index ? confirming : false}
                  setConfirming={setConfirming}
                  offset={activeIndex === index ? offset : 0}
                  setOffset={setOffset}
                  deletingIndex={deletingIndex}
                  forceOffset={editMode ? REVEAL_OFFSET : undefined}
                  loggable={!editMode}
                />
              ))}
              <li className="mb-3">
                <button
                  type="button"
                  className="block px-5 py-3 z-20 shadow-md rounded-lg bg-gray-100 active:bg-powderBlue-200 cursor-pointer w-full max-w-96 mx-auto text-left"
                  onClick={handleNewActivity}
                >
                  <span className=" text-gray-400">Something new</span>
                </button>
              </li>
            </ul>
          </fieldset>
        </form>
        <div className="flex justify-between max-w-96 mx-auto px-6 mt-4 text-base font-semibold text-powderBlue-700">
          <div className="flex flex-col items-center">
            <Link to="/joy/setup/goal" className="flex flex-col items-center">
              <img
                src="https://img.icons8.com/3377bb/ios-glyphs/512/goal.png"
                alt="Goal"
                className="w-8 h-8"
              />
              <span>New goal</span>
            </Link>
          </div>
          <div className="flex flex-col items-center">
            <Link to="/joy/logs" className="flex flex-col items-center">
              <i className="fa-solid fa-clock-rotate-left text-2xl"></i>
              <span>View logs</span>
            </Link>
          </div>
        </div>
      </main>
    </div>
  );
};

export default JoyApp;
