import React from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
  useLocation,
} from "react-router-dom";
import JoyApp from "./joy/JoyApp";
import JoySetupGoal from "./joy/JoySetupGoal";
import JoySetupActivities from "./joy/JoySetupActivities";
import JoyNew from "./joy/JoyNew";
import JoyLogs from "./joy/JoyLogs";
import EPDSForm from "./resources/EPDSForm";
import Home from "./Home";
import WaterApp from "./water/WaterApp";
import WaterLog from "./water/WaterLog";
import Mom from "./Mom";
import Resources from "./resources/Resources";
import MasterCalendar from "./MasterCalendar";

function App() {
  return (
    <Router>
      <MainContent />
      <Footer />
    </Router>
  );
}

function MainContent() {
  const location = useLocation();
  const noMarginPaths = ["/", "/resources", "/calendar"];
  const hasMargin = !noMarginPaths.includes(location.pathname);

  return (
    <div className={`${hasMargin && "mb-16 sm:mb-24"}`}>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/joy" element={<JoyApp />} />
        <Route path="/joy/setup/goal" element={<JoySetupGoal />} />
        <Route path="/joy/setup/activities" element={<JoySetupActivities />} />
        <Route path="/joy/new" element={<JoyNew />} />
        <Route path="/joy/logs" element={<JoyLogs />} />
        <Route path="/water" element={<WaterApp />} />
        <Route path="/water/logs" element={<WaterLog />} />
        <Route path="/mom" element={<Mom />} />
        <Route path="/calendar" element={<MasterCalendar />} />
        <Route path="/resources" element={<Resources />} />
        <Route path="/epds" element={<EPDSForm />} />
      </Routes>
    </div>
  );
}

function Footer() {
  const location = useLocation();

  return (
    <footer className="fixed bottom-0 bg-powderBlue-400 w-full shadow-lg z-50 text-paynesGray-500 rounded-t-xl">
      <nav className="flex sm:justify-around justify-between px-4 sm:px-10 py-2 sm:py-3">
        <Link
          to="/"
          className={`flex flex-col items-center ${
            location.pathname === "/" && "text-paynesGray-800"
          }`}
          aria-label="Home"
        >
          <i className="fa-solid fa-home text-3xl"></i>
          <span className="hidden sm:block text-sm mt-1">Home</span>
        </Link>

        <Link
          to="/calendar"
          className={`flex flex-col items-center ${
            location.pathname === "/calendar" && "text-paynesGray-800"
          }`}
          aria-label="Calendar"
        >
          <i className="fa-solid fa-calendar text-3xl"></i>
          <span className="hidden sm:block text-sm mt-1">Calendar</span>
        </Link>

        <Link
          to="/resources"
          className={`flex flex-col items-center ${
            location.pathname === "/resources" && "text-paynesGray-800"
          }`}
          aria-label="Resources"
        >
          <i className="fa-solid fa-circle-info text-3xl"></i>
          <span className="hidden sm:block text-sm mt-1">Resources</span>
        </Link>

        <Link
          to="https://mchb.hrsa.gov/programs-impact/national-maternal-mental-health-hotline"
          className="flex flex-col items-center"
          aria-label="Get Help"
        >
          <i className="fa-solid fa-hand-holding-heart text-3xl"></i>
          <span className="hidden sm:block text-sm mt-1">Get Help</span>
        </Link>
      </nav>
    </footer>
  );
}

export default App;
