import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./WaterApp.css";

const WaterApp = () => {
  const [volume, setVolume] = useState("");
  const [unit, setUnit] = useState("oz"); // default to ounces
  const [date, setDate] = useState(
    () => new Date().toISOString().split("T")[0]
  );
  const [time, setTime] = useState(() => {
    const now = new Date();
    return now.toTimeString().slice(0, 5);
  });
  const [logs, setLogs] = useState([]);
  const [message, setMessage] = useState("");

  const navigate = useNavigate();

  // Load saved logs
  useEffect(() => {
    const savedLogs = JSON.parse(localStorage.getItem("waterLogs")) || [];
    setLogs(savedLogs);
  }, []);

  const toMl = (val, u) => (u === "oz" ? val * 29.5735 : val);

  const handleAddLog = () => {
    if (!volume) return;
    if (volume <= 0) {
      setMessage("Please enter a positive volume.");
      return;
    }
    const newLog = {
      id: Date.now(),
      date,
      time,
      volume: parseInt(toMl(volume, unit), 10), // always store in ml
    };
    const updatedLogs = [newLog, ...logs];
    setLogs(updatedLogs);
    localStorage.setItem("waterLogs", JSON.stringify(updatedLogs));

    // Reset form
    setVolume("");
    setDate(new Date().toISOString().split("T")[0]);
    setTime(new Date().toTimeString().slice(0, 5));

    setMessage("Log added successfully!");
  };

  return (
    <div className="waterapp-container">
      <h2>Water Intake Log</h2>

      <div style={{ display: "flex", alignItems: "center" }}>
        <input
          className="form-input no-spinner volume-input"
          type="number"
          min="0"
          placeholder={`Volume (${unit})`}
          value={volume}
          onChange={(e) => setVolume(e.target.value)}
        />

        <div className="unit-switch-group">
          <button
            type="button"
            className={`unit-btn ${unit === "ml" ? "active" : ""}`}
            onClick={() => setUnit("ml")}
          >
            ml
          </button>
          <button
            type="button"
            className={`unit-btn ${unit === "oz" ? "active" : ""}`}
            onClick={() => setUnit("oz")}
          >
            oz
          </button>
        </div>
      </div>

      <input
        className="form-input"
        type="date"
        value={date}
        onChange={(e) => setDate(e.target.value)}
      />
      <input
        className="form-input"
        type="time"
        value={time}
        onChange={(e) => setTime(e.target.value)}
      />
      <button className="form-btn primary" onClick={handleAddLog}>
        Add Log
      </button>
      <button
        className="form-btn secondary"
        onClick={() => navigate("/water/logs")}
      >
        View Waterlog History
      </button>
      {message && <div className="success-msg">{message}</div>}
    </div>
  );
};

export default WaterApp;
