import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { DateCalendar } from "@mui/x-date-pickers/DateCalendar";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  IconButton,
  Typography,
  Box,
  Divider,
} from "@mui/material";
import dayjs from "dayjs";
import CloseIcon from "@mui/icons-material/Close";

// MasterCalendar component displays a calendar with events, hydration, and activities for each day
const MasterCalendar = () => {
  // state for selected date in calendar
  const [selectedDate, setSelectedDate] = useState(null);
  // state for pop up open/close
  const [open, setOpen] = useState(false);
  // state for calendar events, loaded from localStorage
  const [events, setEvents] = useState(() => {
    // load events from localstorage on first render
    const stored = localStorage.getItem("calendarEvents");
    return stored ? JSON.parse(stored) : {};
  }); // format: { 'YYYY-MM-DD': [{time, desc}] }
  // state for new event input fields
  const [newEventTime, setNewEventTime] = useState("");
  const [newEventDesc, setNewEventDesc] = useState("");
  // state for editing an event
  const [editingIdx, setEditingIdx] = useState(null); // index of event being edited
  const [editTime, setEditTime] = useState("");
  const [editDesc, setEditDesc] = useState("");
  // state for delete confirmation
  const [deleteIdx, setDeleteIdx] = useState(null); // index of event to delete
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  // state for joy activities and water logs, loaded from localstorage
  const [loggedActivities, setLoggedActivities] = useState({});
  const [waterLogs, setWaterLogs] = useState([]);
  const [meditationLogs, setMeditationLogs] = useState([]);
  // state for water unit preference
  const [waterUnit, setWaterUnit] = useState("oz"); // default to ounces

  // load joy activities and water logs from localstorage
  useEffect(() => {
    try {
      setLoggedActivities(
        JSON.parse(localStorage.getItem("loggedActivities")) || {}
      );
    } catch {
      setLoggedActivities({});
    }
    try {
      setWaterLogs(JSON.parse(localStorage.getItem("waterLogs")) || []);
    } catch {
      setWaterLogs([]);
    }
    try {
      setMeditationLogs(JSON.parse(localStorage.getItem("meditationLogs")) || []);
    } catch {
      setMeditationLogs([]);
    }
  }, []);
  // save events to localstorage whenever events change
  // whenever events change, save them to localstorage
  useEffect(() => {
    localStorage.setItem("calendarEvents", JSON.stringify(events));
  }, [events]);
  // find the closest upcoming event from all events
  function getClosestEvent(events) {
    const now = new Date();
    let closest = null;
    Object.entries(events).forEach(([date, evArr]) => {
      evArr.forEach((ev) => {
        if (!ev.time) return;
        // create a date object for each event
        const eventDate = new Date(date + "T" + ev.time);
        // if event is in the future and closer than previous closest then update
        if (eventDate > now && (!closest || eventDate < closest.date)) {
          closest = { date: eventDate, desc: ev.desc };
        }
      });
    });
    return closest;
  }

  // store the closest upcoming event
  const closestEvent = getClosestEvent(events);

  // when a date is selected in the calendar, open pop up and reset event inputs
  const handleDateChange = (date) => {
    setSelectedDate(date);
    setOpen(true);
    setNewEventTime("");
    setNewEventDesc("");
  };

  // close pop up and reset event inputs
  const handleClose = () => {
    setOpen(false);
    setNewEventTime("");
    setNewEventDesc("");
  };

  // add a new event for the selected date
  const handleAddEvent = () => {
    if (!newEventTime.trim() || !newEventDesc.trim()) return;
    const key = selectedDate.toISOString().slice(0, 10);
    setEvents((prev) => ({
      ...prev,
      [key]: prev[key]
        ? [...prev[key], { time: newEventTime, desc: newEventDesc }]
        : [{ time: newEventTime, desc: newEventDesc }],
    }));
    setNewEventTime("");
    setNewEventDesc("");
  };

  // start editing an event
  const handleEditEvent = (idx) => {
    setEditingIdx(idx);
    setEditTime(eventsForDate[idx].time);
    setEditDesc(eventsForDate[idx].desc);
  };

  // save edited event
  const handleSaveEdit = () => {
    if (!editTime.trim() || !editDesc.trim()) return;
    const key = selectedDate.toISOString().slice(0, 10);
    setEvents((prev) => {
      const updated = [...(prev[key] || [])];
      updated[editingIdx] = { time: editTime, desc: editDesc };
      return { ...prev, [key]: updated };
    });
    setEditingIdx(null);
    setEditTime("");
    setEditDesc("");
  };

  // cancel editing
  const handleCancelEdit = () => {
    setEditingIdx(null);
    setEditTime("");
    setEditDesc("");
  };

  // ask for delete confirmation
  const handleDeleteEvent = (idx) => {
    setDeleteIdx(idx);
    setShowDeleteConfirm(true);
  };

  // confirm deletion
  const confirmDeleteEvent = () => {
    const key = selectedDate.toISOString().slice(0, 10);
    setEvents((prev) => {
      const updated = [...(prev[key] || [])];
      updated.splice(deleteIdx, 1);
      return { ...prev, [key]: updated };
    });
    // If editing the deleted event, cancel edit
    if (editingIdx === deleteIdx) handleCancelEdit();
    setShowDeleteConfirm(false);
    setDeleteIdx(null);
  };

  // cancel deletion
  const cancelDeleteEvent = () => {
    setShowDeleteConfirm(false);
    setDeleteIdx(null);
  };

  // format a time string (HH:mm) as AM/PM
  function formatTime(timeStr) {
    if (!timeStr) return "";
    const [hour, minute] = timeStr.split(":");
    let h = parseInt(hour, 10);
    const m = minute;
    const ampm = h >= 12 ? "PM" : "AM";
    h = h % 12;
    if (h === 0) h = 12;
    return `${h}:${m} ${ampm}`;
  }

  // convert water volume from ml to oz or keep as ml based on unit preference
  function formatWaterVolume(volumeInMl) {
    if (waterUnit === "oz") {
      return `${(volumeInMl / 29.5735).toFixed(1)} oz`;
    } else {
      return `${volumeInMl} ml`;
    }
  }

  // get events, activities, and water logs for the selected date
  let eventsForDate = selectedDate
    ? events[selectedDate.toISOString().slice(0, 10)] || []
    : [];
  const dateKey = selectedDate ? selectedDate.toISOString().slice(0, 10) : null;
  const activitiesForDate =
    dateKey && loggedActivities[dateKey] ? loggedActivities[dateKey] : [];
  const waterLogsForDate = dateKey
    ? waterLogs.filter((log) => log.date === dateKey)
    : [];
  const meditationLogsForDate = dateKey
    ? meditationLogs.filter((log) => log.date === dateKey)
    : [];
  // sort events by time (earliest to latest)
  eventsForDate = [...eventsForDate].sort((a, b) => {
    if (!a.time || !b.time) return 0;
    return a.time.localeCompare(b.time);
  });

  // render calendar, pop up, and sections for events, hydration, activities, and mindfulness
  return (
    <div className="bg-sand text-paynesGray min-h-screen">
      <header className="bg-powderBlue-300 w-full rounded-b-xl shadow-lg text-center p-4 sm:py-5 fixed top-0 left-0 z-10">
        <h1 className="w-full text-2xl sm:text-4xl  text-paynesGray font-bold">
          Calendar
        </h1>
      </header>

      <main className="pt-28 pb-8 px-4 flex flex-col items-center justify-center">
        {/* Upcoming Event Banner */}
        {closestEvent && (
          <div className="w-full max-w-3xl mb-4 mx-auto">
            <div className="bg-yellow-200 border-l-4 border-yellow-500 text-yellow-900 px-4 py-3 rounded shadow text-center">
              <span className="font-bold">Upcoming Event:</span> You have an
              event on{" "}
              <span className="font-semibold">
                {closestEvent.date.toLocaleDateString()}
              </span>{" "}
              at{" "}
              <span className="font-semibold">
                {formatTime(closestEvent.date.toTimeString().slice(0, 5))}
              </span>
              : <span className="italic">{closestEvent.desc}</span>
            </div>
          </div>
        )}
        <div className="w-full max-w-3xl mb-6">
          <div className="flex justify-center">
            <LocalizationProvider dateAdapter={AdapterDateFns}>
              <DateCalendar
                onChange={handleDateChange}
                value={selectedDate}
                sx={{
                  mx: "auto",
                  width: { xs: 320, sm: 400, md: 500 },
                  "& .MuiPickersCalendarHeader-root": { fontSize: "1.25rem" },
                  "& .MuiDayCalendar-weekDayLabel": { fontSize: "1rem" },
                  "& .MuiDayCalendar-day": {
                    fontSize: "1.1rem",
                    minWidth: 48,
                    minHeight: 48,
                  },
                }}
              />
            </LocalizationProvider>
          </div>
        </div>

					<Dialog open={open} onClose={handleClose} maxWidth="sm" fullWidth>
						<DialogTitle sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', background: '#e3f2fd', borderRadius: '16px 16px 0 0', boxShadow: 2 }}>
							<span className="font-bold text-lg text-paynesGray">{selectedDate ? selectedDate.toLocaleDateString() : ''}</span>
							<div style={{ display: 'flex', gap: '8px', alignItems: 'center' }}>
								<IconButton onClick={handleClose}>
									<CloseIcon />
								</IconButton>
							</div>
						</DialogTitle>
						<DialogContent sx={{ background: '#fff', minHeight: { xs: 300, sm: 350 }, borderRadius: '0 0 16px 16px' }}>
						<Box mb={2}>
							<Typography variant="h6" color="primary" className="flex items-center gap-2">Events</Typography>
							<Divider sx={{ mb: 1 }} />
							<Box mb={2}>
							{eventsForDate.length === 0 ? (
								<Typography variant="body2" color="textSecondary">No events for this day.</Typography>
							) : (
								<ul className="mb-2">
									{eventsForDate.map((ev, idx) => (
										<li key={idx} className="mb-2 bg-white rounded-xl shadow px-3 py-2">
											{editingIdx === idx ? (
												<div className="flex flex-col gap-2">
													<div className="flex flex-col sm:flex-row gap-2">
														<input
															type="time"
															value={editTime}
															onChange={e => setEditTime(e.target.value)}
															className="border border-pink-300 rounded px-2 py-1 flex-1"
														/>
														<input
															type="text"
															value={editDesc}
															onChange={e => setEditDesc(e.target.value)}
															className="border border-pink-300 rounded px-2 py-1 flex-2"
														/>
													</div>
													<div className="flex gap-2 justify-end">
														<button
															onClick={handleSaveEdit}
															className="px-4 py-2 sm:px-3 sm:py-1 rounded-lg bg-green-400 text-white font-medium shadow hover:bg-green-500 transition-colors text-sm"
														>Save</button>
														<button
															onClick={handleCancelEdit}
															className="px-4 py-2 sm:px-3 sm:py-1 rounded-lg bg-powderBlue-300 text-paynesGray font-medium shadow hover:bg-powderBlue-200 transition-colors text-sm"
														>Cancel</button>
													</div>
												</div>
											) : (
												<div className="flex flex-col sm:flex-row sm:items-center gap-2">
													<div className="flex-1">
														<span className="font-bold text-pink-500">{formatTime(ev.time)}</span>
														<span className="text-gray-700 ml-2">{ev.desc}</span>
													</div>
													<div className="flex gap-2 sm:ml-auto">
														<button
															onClick={() => handleEditEvent(idx)}
															className="px-4 py-2 sm:px-3 sm:py-1 rounded-lg bg-yellow-300 text-paynesGray font-medium shadow hover:bg-yellow-200 transition-colors text-sm flex-1 sm:flex-none"
														>Edit</button>
														<button
															onClick={() => handleDeleteEvent(idx)}
															className="px-4 py-2 sm:px-3 sm:py-1 rounded-lg bg-red-400 text-white font-medium shadow hover:bg-red-500 transition-colors text-sm flex-1 sm:flex-none"
														>Delete</button>
													</div>
												</div>
											)}
										</li>
									))}
								</ul>
							)}
			{/* Delete Confirmation Dialog */}
			<Dialog open={showDeleteConfirm} onClose={cancelDeleteEvent} maxWidth="xs">
				<DialogTitle sx={{ background: '#fff', borderRadius: '16px 16px 0 0', boxShadow: 2 }}>
					<span className="font-bold text-lg text-paynesGray">Confirm Delete</span>
				</DialogTitle>
				<DialogContent sx={{ background: '#fff', borderRadius: '0 0 16px 16px' }}>
					<Typography variant="body1" className="mb-4 text-paynesGray">
						Are you sure you want to delete this event?
					</Typography>
					<div className="flex justify-end gap-2 mt-2">
						<button
							onClick={confirmDeleteEvent}
							className="px-4 py-2 rounded-lg bg-red-400 text-white font-medium shadow hover:bg-red-500"
						>Delete</button>
						<button
							onClick={cancelDeleteEvent}
							className="px-4 py-2 rounded-lg bg-powderBlue-300 text-paynesGray font-medium shadow hover:bg-powderBlue-200"
						>Cancel</button>
						
					</div>
				</DialogContent>
			</Dialog>
								<div className="flex flex-col sm:flex-row gap-2 mt-2">
									<input
										type="time"
										value={newEventTime}
										onChange={e => setNewEventTime(e.target.value)}
										className="border-2 border-teal-300 rounded px-2 py-1 flex-1 focus:outline-none focus:ring-2 focus:ring-teal-400"
									/>
									<input
										type="text"
										value={newEventDesc}
										onChange={e => setNewEventDesc(e.target.value)}
										placeholder="Event description..."
										className="border-2 border-pink-200 rounded px-2 py-1 flex-2 focus:outline-none focus:ring-2 focus:ring-pink-300"
									/>
													<button
														onClick={handleAddEvent}
														className="px-4 py-1 rounded-lg bg-powderBlue-300 text-paynesGray font-medium shadow hover:bg-powderBlue-200 transition-colors"
													>
														Add Event
													</button>
								</div>
							</Box>
						</Box>
												<Box mb={2}>
													<div className="flex items-center gap-2">
														<Typography variant="h6" style={{ color: '#65b2ca' }}>Hydration</Typography>
														<div className="flex gap-1">
															<button
																type="button"
																className={`px-2 py-1 text-xs rounded ${waterUnit === "oz" ? "bg-blue-500 text-white" : "bg-gray-200 text-gray-700"} hover:bg-blue-400 transition-colors`}
																onClick={() => setWaterUnit("oz")}
															>
																oz
															</button>
															<button
																type="button"
																className={`px-2 py-1 text-xs rounded ${waterUnit === "ml" ? "bg-blue-500 text-white" : "bg-gray-200 text-gray-700"} hover:bg-blue-400 transition-colors`}
																onClick={() => setWaterUnit("ml")}
															>
																ml
															</button>
														</div>
														<Link to="/water" className="bg-[#65b2ca] hover:bg-[#1f93b6] text-white px-2 py-1 rounded shadow text-xs font-bold transition-colors">Go</Link>
													</div>
													<Divider sx={{ mb: 1, background: '#65b2ca' }} />
														{waterLogsForDate.length === 0 ? (
															<Typography variant="body2" color="textSecondary">No hydration logs for this day.</Typography>
														) : (
															<ul className="mb-2">
																{waterLogsForDate.map((log) => (
																	<li key={log.id} className="mb-2 flex items-center gap-2 bg-blue-50 rounded shadow px-3 py-2">
																		<span className="font-bold text-blue-500">{formatTime(log.time)}</span>
																		<span className="text-gray-700">{formatWaterVolume(log.volume)}</span>
																	</li>
																))}
															</ul>
														)}
									</Box>
												<Box mb={2}>
													<div className="flex items-center gap-2">
														<Typography variant="h6" style={{ color: '#186690' }}>Activities</Typography>
														<Link to="/joy" className="bg-[#186690] hover:bg-[#1f93b6] text-white px-2 py-1 rounded shadow text-xs font-bold transition-colors">Go</Link>
													</div>
													<Divider sx={{ mb: 1, background: '#186690' }} />
														{activitiesForDate.length === 0 ? (
															<Typography variant="body2" color="textSecondary">No activities logged for this day.</Typography>
														) : (
															<ul className="mb-2">
																{activitiesForDate.map(([activity, time], idx) => (
																	<li key={idx} className="mb-2 flex items-center gap-2 bg-green-50 rounded shadow px-3 py-2">
																		<span className="text-gray-700">{activity}</span>
																		<span className="font-bold text-green-500">{dayjs(time).format('h:mm A')}</span>
																	</li>
																))}
															</ul>
														)}
									</Box>
                  
                  {/*
									<Box mb={2}>
										<div className="flex items-center gap-2">
											<Typography variant="h6" style={{ color: '#75804c' }}>Meditation</Typography>
											<Link to="/meditation" className="bg-[#75804c] hover:bg-[#5a6038] text-white px-2 py-1 rounded shadow text-xs font-bold transition-colors">Go</Link>
										</div>
										<Divider sx={{ mb: 1, background: '#75804c' }} />
										{meditationLogsForDate.length === 0 ? (
											<Typography variant="body2" color="textSecondary">No meditation sessions for this day.</Typography>
										) : (
											<ul className="mb-2">
												{meditationLogsForDate.map((log) => (
													<li key={log.id} className="mb-2 flex items-center gap-2 bg-green-50 rounded shadow px-3 py-2">
														<span className="font-bold text-green-600">{log.startTime}</span>
														<span className="text-gray-700">{log.minutes.toFixed(1)} min</span>
														{log.completed && <span className="text-green-500 text-xs"> Completed</span>}
													</li>
												))}
											</ul>
										)}
									</Box> */}
					</DialogContent>
				</Dialog>
			</main>
		</div>
	);
};

export default MasterCalendar;
