import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

const JoySetupGoal = (props) => {
  const navigate = useNavigate();

  // State for slider value
  const [sliderValue, setSliderValue] = useState(3);
  const [sliderPercent, setSliderPercent] = useState(200 / 19);

  // Load the default value from localStorage on component mount
  useEffect(() => {
    const storedGoal = localStorage.getItem("dailyActivitiesGoal");
    if (storedGoal) {
      const goalValue = Number(storedGoal);
      setSliderValue(goalValue);
      setSliderPercent(((goalValue - 1) * 100) / 19); // Calculate the percentage
    }
  }, []);

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();

    localStorage.setItem("dailyActivitiesGoal", sliderValue.toString());

    navigate("/joy");
  };

  return (
    <form onSubmit={handleSubmit}>
      <fieldset className="p-4">
        <legend className="text-xl font-semibold pt-4">
          What's your {localStorage.getItem("dailyActivitiesGoal") && "new "}
          goal for activities logged per day?
        </legend>
        <div className="relative mx-auto my-24">
          <input
            className="w-full border border-paynesGray appearance-none rounded-md h-4"
            style={{
              background: "linear-gradient(to right, #c9e4c5, #3377bb)",
            }}
            type="range"
            min="1"
            max="20"
            value={sliderValue}
            onChange={(e) => {
              setSliderValue(Number(e.target.value));
              setSliderPercent(
                Number((e.target.value - e.target.min) * 100) /
                  (e.target.max - e.target.min)
              );
            }}
          />
          <output
            className="absolute min-w-8 top-7 px-2 py-1 pointer-events-none bg-white shadow rounded-lg font-bold text-center select-none"
            style={{
              left: `calc(${sliderPercent}% - (${4 + sliderPercent * 0.25}px))`,
              transition: "left 50ms",
            }}
          >
            {sliderValue}
          </output>
        </div>
        <button
          type="submit"
          className="block font-semibold rounded-3xl mt-4 bg-powderBlue-700 active:bg-powderBlue-800 text-white px-4 py-2 mx-auto"
        >
          Let's go!
        </button>
      </fieldset>
    </form>
  );
};

export default JoySetupGoal;
