import { logActivity } from "../utils/joyLogger";
import { useNavigate } from "react-router-dom";

const JoyNew = () => {
  const navigate = useNavigate();

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault();
    const form = e.currentTarget;
    const activityName = form.elements.activity.value;
    logActivity(activityName);
    if (form.elements.saveToQuickLog.checked) {
      const likedActivities = JSON.parse(
        localStorage.getItem("likedActivities")
      );
      likedActivities.push(activityName);
      localStorage.setItem("likedActivities", JSON.stringify(likedActivities));
    }
    navigate("/joy"); // return to main Joy page
  };

  return (
    <main className="px-4 max-w-96 mx-auto">
      <form onSubmit={handleSubmit}>
        <legend className="text-xl font-semibold pt-4">
          What did you do to take care of yourself?
        </legend>
        <input
          type="text"
          name="activity"
          placeholder="Something new"
          required
          className="p-3 shadow-md rounded-lg w-full bg-gray-100 mt-4"
        />
        <div className="flex flex-row justify-between items-center mt-4">
          <label className="flex-1">Save activity to quick log list</label>
          <input type="checkbox" name="saveToQuickLog" className="ml-4" />
        </div>
        <button
          type="submit"
          className="block rounded-3xl mt-4 bg-powderBlue-700 active:bg-powderBlue-800 text-white px-4 py-2 mx-auto"
        >
          Let's go!
        </button>
      </form>
    </main>
  );
};

export default JoyNew;
