import dayjs from "dayjs";

export function logActivity(activityName) {
  const today = dayjs().format("YYYY-MM-DD");
  // const time = dayjs().format('HH:mm');

  const loggedActivities =
    JSON.parse(localStorage.getItem("loggedActivities")) || {};
  if (!loggedActivities[today]) loggedActivities[today] = [];
  loggedActivities[today].push([activityName, dayjs().toString()]);
  localStorage.setItem("loggedActivities", JSON.stringify(loggedActivities));
}
