import React, { useState } from "react";

// EPDS questions and answers with reverse scoring info
const questions = [
  {
    text: "I have been able to laugh and see the funny side of things.",
    options: [
      "As much as I always could",
      "Not quite so much now",
      "Definitely not so much now",
      "Not at all",
    ],
    reverse: true,
  },
  {
    text: "I have looked forward with enjoyment to things.",
    options: [
      "As much as I ever did",
      "Rather less than I used to",
      "Definitely less than I used to",
      "Hardly at all",
    ],
    reverse: true,
  },
  {
    text: "I have blamed myself unnecessarily when things went wrong.",
    options: [
      "Yes, most of the time",
      "Yes, some of the time",
      "Not very often",
      "No, never",
    ],
    reverse: false,
  },
  {
    text: "I have been anxious or worried for no good reason.",
    options: [
      "Yes, very often",
      "Yes, sometimes",
      "Hardly ever",
      "No, not at all",
    ],
    reverse: false,
  },
  {
    text: "I have felt scared or panicky for no good reason.",
    options: [
      "Yes, quite a lot",
      "Yes, sometimes",
      "No, not much",
      "No, not at all",
    ],
    reverse: false,
  },
  {
    text: "Things have been getting on top of me.",
    options: [
      "Yes, most of the time I havent been able to cope at all",
      "Yes, sometimes I havent been coping as well as usual",
      "No, most of the time I have coped quite well",
      "No, I have been coping as well as ever",
    ],
    reverse: false,
  },
  {
    text: "I have been so unhappy that I have had difficulty sleeping.",
    options: [
      "Yes, most of the time",
      "Yes, some of the time",
      "Not very often",
      "No, not at all",
    ],
    reverse: false,
  },
  {
    text: "I have felt sad or miserable.",
    options: [
      "Yes, most of the time",
      "Yes, some of the time",
      "Not very often",
      "No, not at all",
    ],
    reverse: false,
  },
  {
    text: "I have been so unhappy that I have been crying.",
    options: [
      "Yes, most of the time",
      "Yes, quite often",
      "Only occasionally",
      "No, never",
    ],
    reverse: false,
  },
  {
    text: "The thought of harming myself has occurred to me.",
    options: ["Yes, quite often", "Sometimes", "Hardly ever", "Never"],
    reverse: false,
  },
];

const EPDSForm = () => {
  const [answers, setAnswers] = useState(Array(10).fill(null));
  const [submitted, setSubmitted] = useState(false);
  const [score, setScore] = useState(null);

  // Calculate EPDS score
  const calculateScore = () => {
    let total = 0;
    answers.forEach((ans, idx) => {
      if (ans === null) return;
      if (questions[idx].reverse) {
        // For reverse-true scored questions (positive ones)
        total += ans; // 0,1,2,3
      } else {
        // Normal questions invert the scale
        total += 3 - ans; // 3,2,1,0
      }
    });
    return total;
  };

  const handleChange = (qIdx, value) => {
    const updated = [...answers];
    updated[qIdx] = parseInt(value, 10);
    setAnswers(updated);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const totalScore = calculateScore();
    setScore(totalScore);
    setSubmitted(true);
  };

  return (
    <form onSubmit={handleSubmit} className="max-w-2xl mx-auto p-4">
      <h2 className="text-2xl font-bold mb-2">Take the Assessment</h2>
      <p className="mb-4">
        <strong>About the Edinburgh Postnatal Depression Scale (EPDS)</strong>
        <br />
        The EPDS is a widely used screening tool designed to identify signs of
        postpartum depression and anxiety. First introduced in 1987, it has been
        extensively validated through research and is recognized for its
        reliability and sensitivity in detecting mood disorders in new mothers.
        While not a diagnostic test, the EPDS provides valuable insight that can
        help guide further conversations with healthcare providers.
      </p>
      <p className="mb-4">
        Instructions: Please select the answer which comes closest to how you
        have felt in the past 7 days  not just how you feel today.
      </p>
      {questions.map((q, qIdx) => (
        <fieldset
          key={qIdx}
          className="mb-6 border border-gray-200 rounded p-4"
        >
          <legend className="font-semibold">
            {qIdx + 1}. {q.text}
          </legend>
          <div className="flex flex-col mt-2 space-y-2">
            {q.options.map((opt, oIdx) => (
              <label key={oIdx} className="flex items-center">
                <input
                  type="radio"
                  name={`q${qIdx}`}
                  value={oIdx}
                  checked={answers[qIdx] === oIdx}
                  onChange={() => handleChange(qIdx, oIdx)}
                  className="mr-2"
                  required={qIdx === 0}
                />
                {opt}
              </label>
            ))}
          </div>
        </fieldset>
      ))}
      <button
        type="submit"
        className="w-full bg-paynesGray-500 active:bg-paynesGray-700 text-white px-6 py-2 rounded font-semibold mt-4 mb-24"
        disabled={answers.includes(null)}
        title={`${answers.includes(null) ? "Answer every question." : ""}`}
      >
        Submit
      </button>

      {submitted && (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50">
          <div className="bg-white rounded-2xl shadow-lg p-6 max-w-md w-full text-center">
            <h3 className="text-xl font-bold mb-4">Your EPDS Score</h3>
            <p className="text-3xl font-extrabold text-paynesGray-500 mb-2">
              {score}
            </p>
            <p className="mb-6">
              {score >= 10
                ? "A score of 10 or higher suggests a potential risk for postpartum depression. Please consult a healthcare provider."
                : "Your score is below the threshold for postpartum depression risk, but if you have concerns, please talk to your provider."}
            </p>
            <button
              onClick={() => setSubmitted(false)}
              className="bg-paynesGray-500 text-white px-6 py-2 rounded font-semibold hover:bg-paynesGray-500-700"
            >
              Close
            </button>
          </div>
        </div>
      )}
    </form>
  );
};

export default EPDSForm;
